'use client';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import apiClient from '../lib/api-client';

type WO = {
  id: string | number;
  doc_no?: string | null;
  asset_name?: string | null;
  work_type?: string | null;
  start_date?: string | null;
  end_date?: string | null;
  description?: string | null;
  raw?: any;
};

const rowHeight = 44;
const labelWidth = 300;
const gutter = 12;
const minBarWidthPx = 4;

function isoToMs(val?: string | null) {
  if (!val) return null;
  const n = Date.parse(val);
  return isNaN(n) ? null : n;
}

function niceTime(ms: number | null) {
  if (!ms) return '-';
  const d = new Date(ms);
  return d.toLocaleString();
}

function dateInputToDayStart(dateStr: string) {
  // local midnight
  return new Date(`${dateStr}T00:00:00`);
}

const WORK_TYPE_COLORS: Record<string, string> = {
  'Preventive Maintenance': '#16a34a',
  'Corrective Maintenance': '#fb923c',
  'Breakdown': '#ef4444',
  'PM': '#10b981',
  'CM': '#f97316',
  'Default': '#3b82f6',
};

const STATUS_COLORS: Record<string, string> = {
  'Draft': '#9ca3af',
  'Posted': '#06b6d4',
  'Completed': '#10b981',
  'In Progress': '#f59e0b',
  'Cancelled': '#ef4444',
};

function pickColorForWork(w: WO) {
  const status = (w.raw && (w.raw.doc_status || w.raw.status || w.raw.state)) || null;
  if (status && typeof status === 'string' && STATUS_COLORS[status]) return STATUS_COLORS[status];
  const wt = (w.work_type || (w.raw && (w.raw.work_type || w.raw.type_work)) || '').toString();
  if (wt && WORK_TYPE_COLORS[wt]) return WORK_TYPE_COLORS[wt];
  // try shorthand
  if (wt && WORK_TYPE_COLORS[wt.toUpperCase() as keyof typeof WORK_TYPE_COLORS]) return WORK_TYPE_COLORS[wt.toUpperCase() as keyof typeof WORK_TYPE_COLORS];
  return WORK_TYPE_COLORS['Default'];
}

export default function GanttChart({ pageSize = 2000 }: { pageSize?: number }) {
  const [items, setItems] = useState<WO[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const todayIsoDate = new Date().toISOString().slice(0,10);
  const [selectedDate, setSelectedDate] = useState<string>(todayIsoDate);
  const [scale, setScale] = useState<number>(1);
  const [selected, setSelected] = useState<WO | null>(null);

  const containerRef = useRef<HTMLDivElement | null>(null);
  const [containerWidth, setContainerWidth] = useState<number>(900);

  useEffect(() => {
    const el = containerRef.current;
    if (!el) return;
    setContainerWidth(el.clientWidth || 900);
    const ro = new ResizeObserver(() => setContainerWidth(el.clientWidth || 900));
    ro.observe(el);
    return () => ro.disconnect();
  }, []);

  useEffect(() => { load(); }, []);

  async function load() {
    setLoading(true);
    setError(null);
    try {
      const res = await apiClient(`/work-orders?q=&page=1&pageSize=${pageSize}`);
      const rows = res?.data ?? res;
      const mapped: WO[] = (rows || []).map((r: any) => ({
        ...r,
        start_date: r.start_date ?? null,
        end_date: r.end_date ?? null,
        description: r.description ?? r.raw?.description ?? null,
      }));
      setItems(mapped);
    } catch (err: any) {
      console.error('load gantt', err);
      setError(err?.body?.message || err?.message || 'Gagal memuat data');
    } finally {
      setLoading(false);
    }
  }

  const { dayStartMs, dayEndMs } = useMemo(() => {
    const ds = dateInputToDayStart(selectedDate);
    const de = new Date(ds.getTime() + 24*60*60*1000);
    return { dayStartMs: ds.getTime(), dayEndMs: de.getTime() };
  }, [selectedDate]);

  const totalMs = 24 * 60 * 60 * 1000;

  // compute timelineWidth deterministically: use available container width minus label
  const available = Math.max(300, containerWidth - labelWidth - gutter - 20);
  const timelineWidth = available;
  const svgWidth = labelWidth + gutter + timelineWidth + 8;
  const pxPerMs = (timelineWidth / totalMs) * scale;

  function msToX(ms: number) {
    return labelWidth + gutter + Math.round((ms - dayStartMs) * pxPerMs);
  }
  function clampBarWidth(x1: number, x2: number) {
    return Math.max(minBarWidthPx, x2 - x1);
  }

  const tickStepMs = 30 * 60 * 1000;
  const ticks = useMemo(() => {
    const out: number[] = [];
    const startTick = Math.floor(dayStartMs / tickStepMs) * tickStepMs;
    for (let t = startTick; t <= dayEndMs; t += tickStepMs) out.push(t);
    return out;
  }, [dayStartMs, dayEndMs]);

  const validItems = useMemo(() => {
    const filtered = items.filter(it => {
      const s = isoToMs(it.start_date);
      const e = isoToMs(it.end_date);
      if (s == null && e == null) return false;
      const start = s ?? e ?? null;
      const end = e ?? s ?? null;
      if (start == null || end == null) return false;
      return !(end <= dayStartMs || start >= dayEndMs);
    });
    return filtered.sort((a,b) => {
      const sa = isoToMs(a.start_date) ?? 0;
      const sb = isoToMs(b.start_date) ?? 0;
      return sa - sb;
    });
  }, [items, dayStartMs, dayEndMs]);

  const nowMs = Date.now();
  const showNow = nowMs >= dayStartMs && nowMs <= dayEndMs;
  const nowX = showNow ? msToX(nowMs) : null;

  return (
    <div style={{ padding: 18, fontFamily: 'Inter, system-ui, -apple-system, Roboto, "Helvetica Neue", Arial' }}>
      <div style={{ display:'flex', alignItems:'center', gap:8, marginBottom:12 }}>
        <h2 style={{ margin:0 }}>Gantt Chart — Work Orders (per-hari)</h2>
        <div style={{ marginLeft: 'auto', display:'flex', gap:8, alignItems:'center' }}>
          <label style={{ display:'flex', alignItems:'center', gap:8 }}>
            Tanggal:
            <input type="date" value={selectedDate} onChange={e => setSelectedDate(e.target.value)} style={{ marginLeft:8, padding:'6px 8px', borderRadius:6, border:'1px solid #ddd' }} />
          </label>
          <button onClick={() => setScale(s => Math.max(0.5, s * 0.9))}>Zoom -</button>
          <button onClick={() => setScale(s => Math.min(4, s * 1.1))}>Zoom +</button>
          <button onClick={load}>Refresh</button>
        </div>
      </div>

      {loading && <div>Loading...</div>}
      {error && <div style={{ color:'red' }}>{error}</div>}

      <div ref={containerRef} style={{ overflowX:'auto', border:'1px solid #eee', borderRadius:8 }}>
        <div style={{ display:'flex', minWidth: Math.max(900, svgWidth) }}>
          {/* labels */}
          <div style={{ width: labelWidth, borderRight: '1px solid #f0f0f0', background: '#fafafa' }}>
            <div style={{ padding: '10px 12px', fontWeight:700 }}>Work Order</div>
            <div style={{ height:8 }} />
            {validItems.map((w, idx) => (
              <div key={w.id} style={{
                height: rowHeight,
                display:'flex',
                flexDirection:'column',
                justifyContent:'center',
                padding: '6px 12px',
                borderBottom: '1px solid #f1f1f1',
                whiteSpace: 'nowrap',
                overflow:'hidden',
                textOverflow:'ellipsis'
              }}>
                <div style={{ fontSize:13, fontWeight:700 }}>{w.doc_no ?? w.id}</div>
                <div style={{ marginTop:4, color:'#666', fontSize:12 }}>{w.asset_name ?? ''} {w.work_type ? ` • ${w.work_type}` : ''}</div>
              </div>
            ))}
          </div>

          {/* timeline */}
          <div style={{ flex:'1 0 auto', minWidth: 600, position:'relative' }}>
            <svg width={svgWidth} height={Math.max(140, validItems.length * rowHeight + 80)}>
              {/* background rows */}
              {validItems.map((w, idx) => (
                <rect key={'bg'+w.id}
                  x={labelWidth + gutter}
                  y={idx * rowHeight + 40}
                  width={svgWidth - labelWidth - gutter}
                  height={rowHeight}
                  fill={idx % 2 === 0 ? '#ffffff' : '#fbfbfb'}
                />
              ))}

              {/* ticks (30 min) */}
              {ticks.map((t, i) => {
                const x = msToX(t);
                const showLabel = (t - dayStartMs) % (60*60*1000) === 0; // hourly label
                return (
                  <g key={'tick'+i}>
                    <line x1={x} y1={30} x2={x} y2={30 + Math.max(20, validItems.length * rowHeight + 40)} stroke="#e7e7e7" />
                    {showLabel && <text x={x + 4} y={20} fontSize={11} fill="#333">{new Date(t).toLocaleTimeString([], { hour:'2-digit', minute:'2-digit' })}</text>}
                  </g>
                );
              })}

              {/* now line */}
              {showNow && nowX !== null && (
                <g>
                  <line x1={nowX} y1={30} x2={nowX} y2={30 + Math.max(20, validItems.length * rowHeight + 40)} stroke="#8b5cf6" strokeWidth={2} strokeDasharray="6,4" />
                </g>
              )}

              {/* bars */}
              {validItems.map((w, idx) => {
                const sMsRaw = isoToMs(w.start_date) as number;
                const eMsRaw = isoToMs(w.end_date) as number;
                const sMs = Math.max(sMsRaw, dayStartMs);
                const eMs = Math.min(eMsRaw, dayEndMs);
                if (!sMsRaw || !eMsRaw) return null;
                if (eMs <= sMs) return null;
                const x1 = msToX(sMs);
                const x2 = msToX(eMs);
                const width = clampBarWidth(x1, x2);
                const y = idx * rowHeight + 40 + 6;
                const rx = 6;
                const color = pickColorForWork(w);

                return (
                  <g key={'bar'+w.id} style={{ cursor:'pointer' }} onClick={() => setSelected(w)}>
                    <rect x={x1} y={y} rx={rx} ry={rx} width={width} height={rowHeight - 12} fill={color} opacity={0.98} />
                    <foreignObject x={x1 + 8} y={y + 6} width={Math.max(40, width - 16)} height={rowHeight - 24}>
                      <div xmlns="http://www.w3.org/1999/xhtml" style={{ fontSize:12, color:'#fff', fontWeight:700, lineHeight:'14px', overflow:'hidden', textOverflow:'ellipsis', whiteSpace:'nowrap' }}>
                        {w.asset_name ?? w.work_type ?? ''}
                      </div>
                    </foreignObject>
                  </g>
                );
              })}
            </svg>
          </div>
        </div>
      </div>

      {/* modal */}
      {selected && (
        <div style={{
          position:'fixed', left:0, right:0, top:0, bottom:0, background:'rgba(0,0,0,0.36)',
          display:'flex', justifyContent:'center', alignItems:'center', zIndex:9999
        }}>
          <div style={{ background:'#fff', width:720, maxHeight:'80%', overflowY:'auto', borderRadius:8, padding:16 }}>
            <h3 style={{ marginTop:0 }}>{selected.doc_no}</h3>
            <div><strong>Asset:</strong> {selected.asset_name}</div>
            <div><strong>Type:</strong> {selected.work_type}</div>
            <div><strong>Start:</strong> {niceTime(isoToMs(selected.start_date))}</div>
            <div><strong>End:</strong> {niceTime(isoToMs(selected.end_date))}</div>
            <div style={{ marginTop:12 }}>
              <pre style={{ whiteSpace:'pre-wrap', background:'#f7f7f7', padding:8, borderRadius:6 }}>{selected.description ?? JSON.stringify(selected.raw ?? selected, null, 2)}</pre>
            </div>
            <div style={{ textAlign:'right', marginTop:8 }}>
              <button onClick={() => setSelected(null)} style={{ padding:'8px 12px', borderRadius:6 }}>Close</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
